.page__line { position: absolute; top: 0; bottom: 0; border-left: 1px solid gainsboro; border-right: 1px solid gainsboro; width: 20%; opacity: 0.5; transform: scaleY(0); transform-origin: center bottom; }

@media only screen and (max-width: 767px) { .page__line { border-left: none; width: 1px; } }

.page__line:nth-child(1) { left: 0; }

@media only screen and (max-width: 767px) { .page__line:nth-child(1) { left: 50%; } }

.page__line:nth-child(2) { left: 40%; }

@media only screen and (max-width: 767px) { .page__line:nth-child(2) { display: none; } }

.page__line:nth-child(3) { left: 80%; }

@media only screen and (max-width: 767px) { .page__line:nth-child(3) { display: none; } }

.page__label { position: fixed; left: 45px; bottom: 45px; max-height: 100px; color: #d1d1d1; font-size: 9px; font-weight: 500; line-height: 11px; letter-spacing: 0.005em; text-align: left; writing-mode: vertical-rl; transform: scale(-1); }

@media only screen and (max-width: 1025px) { .page__label { position: relative; left: 0; bottom: 0; float: right; padding: 15px 30px 40px; max-width: 240px; max-height: none; text-align: right; writing-mode: inherit; transform: scale(1) !important; } }

@media only screen and (max-width: 1025px) { .team__title { margin-bottom: 10px; } }

