.news {
	padding: 0 45px;
	margin-right: -27%;
	@include md {
		padding: 0;
		margin-right: 0; }
	@include sm {
		padding-bottom: 20px; }
	@include xs {
		padding-top: 15px;
		padding-bottom: 30px; }
	&__items {
		margin: 0 -35px;
		font-size: 0;
		@include md {
			margin: 0 -25px; }
		@include xs {
			margin: 0; } }
	&__item {
		display: inline-block;
		vertical-align: top;
		padding: 25px 35px;
		width: 33.33%;
		@include md {
			padding: 25px; }
		@include sm {
			width: 50%; }
		@include xs {
			padding: 8px 0;
			width: 100%; }
		&_main {
			display: block;
			min-width: 100%; } }
	&__heading {
		margin-bottom: 20px;
		@include xs {
			margin-bottom: 10px; } }
	&__title {
		@include md {
			margin-bottom: 10px; } }
	&__underline {
		display: block;
		border: none;
		margin: 0;
		border-bottom: 2px solid #060416;
		max-width: 152px; }
	&__subtitle {
		max-width: 185px;
		@include f(14px, 700, 18px); }
	&__img {
		position: relative;
		padding-bottom: 56.25%;
		margin-bottom: 10px;
		@include cover; }
	&__icon {
		padding: 30px 0;
		margin-bottom: 10px; }
	&__icon-in {
		fill: $blue;
		font-size: 58px; }
	&__box {
		padding: 30px 0 30px 30px;
		@include xs {
			padding: 17px 0 8px; }
		&_simple {
			padding-left: 0; } }
	&__date {
		margin-bottom: 3px;
		color: $blue;
		@include f(13px, 700, 24px); }
	&__description {
		max-width: 240px;
		color: $black;
		@include f(12px, 700, 18px); } }
