[data-animation-from="fade"] {
	opacity: 0; }

[data-animation-from="fade-top"] {
	opacity: 0;
	transform: translateY(-30px); }

[data-animation-from="fade-bottom"] {
	opacity: 0;
	transform: translateY(30px); }

[data-animation-from="scale-x"] {
	transform: scaleX(0);
	transform-origin: left center; }

