.btn {
	color: $blue;
	background: none;
	font-family: $fontPrimary;
	@include vertical;
	@include sm {
		padding: 30px;
		width: 100%;
		color: $white;
		background: $blue; }
	@include hover {
		.btn__arrow {
			transform: translate3d(5px,0,0); } }
	&__text,
	&__arrow {
		display: inline-block;
		vertical-align: middle; }
	&__text {
		@include f(12px, 400, 24px);
		letter-spacing: 0.001em;
		@include sm {
			@include f(16px, 700, 1); } }
	&__arrow {
		margin-left: 15px;
		fill: currentColor;
		font-size: 21px;
		transition: transform 0.3s;
		@include sm {
			display: none; } } }

.sm-btn {
	display: inline-block;
	vertical-align: top;
	padding: 10px 16px;
	color: $white;
	background: $blue;
	font-family: $fontPrimary;
	@include f(18px, 600, 1);
	text-decoration: none;
	@include md {
		padding: 8px 12px;
		font-size: 14px; }
	@include xxs {
		font-size: 12px; } }
