.title-xl {
	font-family: $fontGotham;
	@include f(48px, 700, 1.25);
	letter-spacing: 0.001em;
	@include lg {
		font-size: 42px; }
	@include md {
		@include f(36px, 900, 1.111); }
	&_black {
		font-family: $fontPrimary;
		font-weight: 900; }
	&_xs-sm {
		@include xs {
			font-size: 26px; } } }

.title-lg {
	@include f(38px, 900, 1.11);
	letter-spacing: 0.001em; }

.title-md {
	@include f(36px, 900, 1.11);
	letter-spacing: 0.001em; }

.title-sm {
	@include f(24px, 900, 1.25);
	letter-spacing: 0.001em;
	@include xs {
		font-size: 20px; } }
