.header {
	position: fixed;
	z-index: 500;
	top: 0;
	left: 0;
	right: 0;
	min-width: 320px;
	transition: background 0.3s, box-shadow 0.3s;
	&[data-theme="white"] {
		.btn-nav {
			span {
				background: $white; } }
		.logo {
			color: $white; }
		.header__link {
			color: $white; } }
	&.is-active {
		@include md {
			background: $white;
			box-shadow: 0 2px 3px 0 rgba($black, 0.1); }
		.btn-nav {
			span {
				@include md {
					background: $blue; } } }
		.logo {
			@include md {
				color: $blue; } } }
	&__btn {
		position: absolute;
		z-index: 200;
		top: 50px;
		right: 45px;
		@include md {
			top: 23px;
			right: 30px; } }
	&__link {
		position: absolute;
		z-index: 200;
		top: 55px;
		right: 275px;
		color: $blue;
		@include f(18px, 700, 1.3);
		text-decoration: none;
		transition: color 0.3s;
		@include md {
			display: none; }
		@include hover {
			text-decoration: underline; } }
	&__inner {
		padding-top: 34px;
		min-height: 70px;
		@include md {
			padding-top: 10px;
			padding-bottom: 10px; } } }

.btn-nav {
	position: absolute;
	z-index: 200;
	top: 43px;
	left: 25px;
	padding: 20px;
	background: none;
	@include md {
		top: 10px;
		left: 10px; }
	@include hover {
		span:last-child {
			transform: scaleX(1); } }
	span {
		display: block;
		width: 17px;
		height: 3px;
		background: $blue;
		transition: transform 0.3s, background 0.3s;
		@include md {
			width: 24px;
			height: 4px; }
		&:not(:last-child) {
			margin-bottom: 2px; }
		&:last-child {
			transform: scaleX(0.53);
			transform-origin: left center; } } }

.logo {
	float: left;
	margin-left: 45px;
	color: $blue;
	font-family: $fontMyriad;
	font-size: 0;
	transition: color 0.3s;
	@include md {
		float: right;
		margin-right: 170px; }
	@include xxs {
		margin: 10px 150px 0 0; }
	&__icon {
		fill: currentColor;
		font-size: 56px;
		@include md {
			font-size: 47px; }
		@include xxs {
			font-size: 30px; } } }
